import {makePostRequest, makeGetRequest} from "./httpHelperFetch";

const baseUrl = "https://torgetapiappstaging.azurewebsites.net/api/webchat"

export const initWebChat = async (name: string, msg: string, torgetUserId: string) => {
    try{
   return  await makePostRequest<IWebChatModel>(`${baseUrl}/chats`, {torgetUserId, message: msg, name});
    }
    catch(e){
        console.log(e);
    }
    return null;
}

export const sendMessage = async (chatId: string, torgetUserId: string, message: string) => {
    try{
        return  await makePostRequest(`${baseUrl}/messages/${chatId}`, { message, torgetUserId });
         }
         catch(e){
             console.log(e);
         }
         return null;
}

export const convertToEmail = async (chatId: string, torgetUserId: string, email?: string) => {
    try{
        return  await makePostRequest(`${baseUrl}/toemail/${chatId}`, { email, torgetUserId });
         }
         catch(e){
             console.log(e);
         }
         return null;
}

export const getChatMessages =  async (chatId: string) => {
    try{
        return  await makeGetRequest<IWebChatObject[]>(`${baseUrl}/${chatId}`);
         }
         catch(e){
             console.log(e);
         }
         return null;
}


export const getIncrementalChatMessages =  async (chatId: string, msgId: string) => {
    try{
        return  await makeGetRequest<IWebChatMessage[]>(`${baseUrl}/${chatId}/${msgId}`);
         }
         catch(e){
             console.log(e);
         }
         return null;
}

export const getChatPersons =  async (chatId: string) => {
    try{
        return  await makeGetRequest<IChatPerson[]>(`${baseUrl}/persons/${chatId}`);
         }
         catch(e){
             console.log(e);
         }
         return null;
}
