import React, { Component } from 'react';
import Launcher from '../../components/Launcher';
import {
    initWebChat,
    sendMessage,
    getChatMessages,
    getIncrementalChatMessages,
    convertToEmail,
    getChatPersons
} from "../../lib/apiHelper";

import './../../styles';

interface IChatWidgetState {
    messageList: IMessage[];
    newMessagesCount: number;
    isOpen: boolean;
    isChatStarted: boolean;
    waitCounter: number;
    yourname: string;
    userId?: string;
    chatId?: string;
    lastMessageId?: string;
    showChatByMail: boolean;
    isFullScreen: boolean;
}

const getQueryStringValue = (key: string) => {
    return decodeURIComponent(window.location.search.replace(new RegExp("^(?:.*[&\\?]" + encodeURIComponent(key).replace(/[\.\+\*]/g, "\\$&") + "(?:\\=([^&]*))?)?.*$", "i"), "$1"));
};

class ChatWidget extends Component<{}, IChatWidgetState> {
    timerId: any;
    constructor(props: any) {
        super(props);
        this.state = {
            messageList: [],
            newMessagesCount: 0,
            isOpen: false,
            isChatStarted: false,
            waitCounter: 0,
            yourname: '',
            showChatByMail: false,
            isFullScreen: false
        };
        const chatId = getQueryStringValue("id");
        if (chatId) {
            setTimeout(() => { this._initiateChat(chatId) }, 100);
        }
    }

    componentWillUnmount() {
        if (this.timerId) {
            clearInterval(this.timerId);
        }
    }

    _initiateChat = (chatId: string) => {
        this.setState({ chatId, isFullScreen: true, isOpen: true, isChatStarted: true }, async () => {
            const users = await getChatPersons(chatId);
            if (users) {
                console.log("users -- ", users);
                for (var key in users) {
                    if (key !== "status") {
                        this.setState({ userId: users[key].id, yourname: users[key].name },
                            async () => {
                                this.timerId = setInterval(() => this._pullMessages(), 5000);
                            });
                    }
                }
            }
        });
    }

    _pullMessages = async () => {
        const { lastMessageId, chatId } = this.state;
        if (!lastMessageId && chatId) {
            const chatMsgObject = await getChatMessages(chatId);
            if (chatMsgObject) {
                chatMsgObject[0].chatMessages.forEach(message => {
                    this._sendMessage(message);
                });
            }
        }
        else if (lastMessageId && chatId) {
            const chatMessages = await getIncrementalChatMessages(chatId, lastMessageId);
            if (chatMessages) {

                for (var key in chatMessages) {
                    if (key !== "status") {
                        this._sendMessage(chatMessages[key]);
                    }
                }
            }
        }
        this.setState((prevState: any) => {
            return { waitCounter: prevState.waitCounter + 1 }
        }, () => {
            if (this.state.waitCounter === 10) {
                console.log("Show email option");
                this.setState({ showChatByMail: true });
            }
        });
    }

    _onMessageWasSent = async (message: IMessage) => {
        const { isChatStarted, yourname, lastMessageId, chatId, userId } = this.state;
        if (!isChatStarted) {
            // call first api
            console.log('hi', message);
            this.setState({
                isChatStarted: true,
                yourname: message.data.text || 'You'
            });
        }
        else if (message.data.text) {

            // call other
            // this.setState({
            //      messageList: [...this.state.messageList, message]
            //  });
            if (!lastMessageId) {
                const chat = await initWebChat(yourname, message.data.text, window._chatToUser);
                if (chat && chat.userId) {
                    this.setState({
                        userId: chat.userId,
                        chatId: chat.chatId
                    });
                }
                console.log(' this is  chat ', chat);
                this.timerId = setInterval(() => this._pullMessages(), 5000);
            }
            else if (chatId && userId) {
                await sendMessage(chatId, userId, message.data.text);
            }
            this._pullMessages();
        }
    }

    _onFilesSelected(fileList: any) {
        /*  const objectURL = window.URL.createObjectURL(fileList[0]);
          this.setState({
              messageList: [...this.state.messageList, {
                  type: 'file', author: 'me',
                  data: {
                      url: objectURL,
                      fileName: fileList[0].name
                  }
              }]
          });*/
    }

    _sendMessage(msg: IWebChatMessage) {
        if (msg) {
            const { userId } = this.state;
            const newMessagesCount = this.state.isOpen ? this.state.newMessagesCount : this.state.newMessagesCount + 1;
            this.setState({
                newMessagesCount: newMessagesCount,
                messageList: [...this.state.messageList, {
                    author: msg.authorId === userId ? "me" : "them",
                    type: 'text',
                    data: { text: msg.message },
                    id: msg.id,
                    created: msg.created
                }],
                lastMessageId: msg.id,
                waitCounter: 0,
                showChatByMail: false
            });
        }
    }

    _handleClick() {
        this.setState({
            isOpen: !this.state.isOpen,
            newMessagesCount: 0
        });
    }

    _onEndChat = async (endChat: IEmailChat | null | undefined) => {
        const { chatId, userId } = this.state;
        if (this.timerId) {
            clearInterval(this.timerId);
            if (chatId && userId) {
                if (endChat && endChat.sendAsEmail) {
                    await convertToEmail(chatId, userId, endChat.email);
                    console.log("call api Here for email reply");
                }
                await sendMessage(chatId, userId,"Visitor did not leave their email, conversation inactive.");
            }
            this.setState({
                waitCounter: 0,
                lastMessageId: undefined,
                chatId: undefined,
                userId: undefined,
                messageList: [],
                isChatStarted: false,
                yourname: "",
                isOpen: false,
                showChatByMail: false
            });
        }
        else {
            this.setState({
                isOpen: !this.state.isOpen,
                newMessagesCount: 0
            });
        }
    }

    render() {
        const { yourname, isChatStarted, isOpen, newMessagesCount, messageList, showChatByMail, isFullScreen } = this.state;
        return <div>
            <Launcher
                agentProfile={{
                    teamName: window._teamName,
                    imageUrl: window._imageUrl
                }}
                onMessageWasSent={this._onMessageWasSent.bind(this)}
                onFilesSelected={this._onFilesSelected.bind(this)}
                messageList={messageList}
                newMessagesCount={newMessagesCount}
                handleClick={this._handleClick.bind(this)}
                isOpen={isOpen}
                onEndChat={this._onEndChat}
                isChatStarted={isChatStarted}
                yourname={yourname}
                showChatByMail={showChatByMail}
                isFullScreen={isFullScreen}
            />
        </div>;
    }
}

export default ChatWidget;