import React, { PureComponent } from 'react';
//import closeIcon from './../assets/close-icon.png';

interface HeaderProps {
  imageUrl?: string;
  teamName?: string;
  onMinimize: () => void;
  onMaximize: () => void;
  onClose: () => void;
  isMaximized: boolean;
}

class Header extends PureComponent<HeaderProps> {

  render() {
    const { imageUrl, onClose, onMinimize, onMaximize, isMaximized } = this.props;
    return (
      <div className="sc-header">
        {imageUrl &&
          <img className="sc-header--img" src={imageUrl} alt="" />}
        <div className="sc-header--team-name"> Support Chat </div>
        <div className="sc-header--close-button" onClick={onMinimize}>
          <i className="far fa-window-minimize" />
        </div>
        <div className="sc-header--close-button" onClick={onMaximize}>
          {!isMaximized &&
            <i className="far fa-window-maximize" />}
          {isMaximized &&
            <i className="far fa-window-restore" />}
        </div>
        <div className="sc-header--close-button" onClick={onClose}>
          <i className="fas fa-times" />
        </div>
      </div>
    );
  }
}

export default Header;
