import React from 'react';
import Linkify from 'react-linkify';
import Moment from 'react-moment';

const TextMessage = (props: IMessage) => {

  return <div className="sc-message--text">
    <div className="sc-message--author-wrapper">
      <div className="sc-message--author">{props.author === "me" ? "You" : window._teamName}</div>
      <Moment fromNow>{props.created.toString()}</Moment>
    </div>
    {<Linkify properties={{ target: '_blank' }}>{props.data.text}</Linkify>}
  </div>;
};

export default TextMessage;
