import React, { Component } from 'react';
import MessageList from './MessageList';
import UserInput from './UserInput';
import Header from './Header';

interface IChatWindowState {
    isMaximized: boolean;
    email: string
}

interface IChatWindowProps {
    isChatStarted: boolean;
    agentProfile: any;
    onClose: (endChat: IEmailChat | null | undefined) => void;
    onMinimize: () => void;
    yourname?: string;
    isOpen: boolean;
    messageList: IMessage[];
    onUserInputSubmit: (message: IMessage) => void;
    onFilesSelected: (message: IMessage) => void;
    showChatByMail: boolean;
    isFullScreen: boolean;
}

class ChatWindow extends Component<IChatWindowProps, IChatWindowState> {

    constructor(props: any) {
        super(props);
        this.state = { isMaximized: false, email: "" };
    }

    onUserInputSubmit(message: any) {
        this.props.onUserInputSubmit(message);
    }

    onFilesSelected(filesList: any) {
        this.props.onFilesSelected(filesList);
    }

    handleMaximize = () => {
        this.setState((prevState: any) => {
            return { isMaximized: !prevState.isMaximized }
        });
    }

    render() {
        const { isChatStarted,
            agentProfile,
            onClose,
            onMinimize,
            yourname,
            isOpen,
            showChatByMail, isFullScreen } = this.props;
        const { email, isMaximized } = this.state;
        const messageList = this.props.messageList || [];
        const classList = [
            'sc-chat-window',
            (isOpen ? 'opened' : 'closed'),
            (isMaximized || isFullScreen ? "maximized" : "")
        ];

        return (
            <div className={classList.join(' ')}>
                {!isFullScreen && <Header
                    teamName={agentProfile.teamName}
                    imageUrl={agentProfile.imageUrl}
                    onClose={() => {
                        onClose(null);
                    }}
                    onMaximize={this.handleMaximize}
                    onMinimize={onMinimize}
                    isMaximized={isMaximized}
                />}
                {!isChatStarted &&
                    <div className="sc-chat-Welcome">
                        Chat with support
            </div>
                }
                {isChatStarted && <div className="sc-chat-Welcome">{`Welcome ${yourname}!`}</div>}
                {isChatStarted &&
                    <MessageList
                        messages={messageList}
                        imageUrl={agentProfile.imageUrl}
                    />
                }
                {showChatByMail &&
                    <div className="sc-chat--email">
                        <div className="sc-chat-Welcome">
                            Company is not availiable right now, do you wish to recive the reply on email instead?
            </div>
                        <input className="sc-chat--email-input" type="email" placeholder="Enter your email address." value={email}
                            onChange={(event) => this.setState({ email: event.target.value })}
                        />
                        <div className="sc-message--email-btn-wrapper">
                            <button className="sc-message--email-btn"
                                onClick={() => {
                                    onClose({ sendAsEmail: true, email });
                                }}
                            >Yes</button>
                            <button className="sc-message--email-btn"
                                onClick={() => {
                                    onClose({ sendAsEmail: false });
                                }}
                            >No</button>
                        </div>
                    </div>
                }
                <UserInput
                    onSubmit={this.onUserInputSubmit.bind(this)}
                    onFilesSelected={this.onFilesSelected.bind(this)}
                    isChatStarted={isChatStarted}
                />
            </div>
        );
    }
}

export default ChatWindow;
