import React, { Component } from 'react';
import ChatWindow from './ChatWindow';
import launcherIcon from './../assets/appicon.png';
//import incomingMessageSound from './../assets/sounds/notification.mp3';
import launcherIconActive from './../assets/close-icon.png';

class Launcher extends Component<any, any> {

  constructor(props: any) {
    super(props);
    this.state = {
      launcherIcon,
      isOpen: false
    };
  }

  componentWillReceiveProps(nextProps: any) {
    if (this.props.mute) { return; }
    const nextMessage = nextProps.messageList[nextProps.messageList.length - 1];
    const isIncoming = (nextMessage || {}).author === 'them';
    const isNew = nextProps.messageList.length > this.props.messageList.length;
    if (isIncoming && isNew) {
      this.playIncomingMessageSound();
    }
  }

  playIncomingMessageSound() {
    //var audio = new Audio(incomingMessageSound);
    //audio.play();
  }

  handleClick() {
    if (this.props.handleClick !== undefined) {
      this.props.handleClick();
    } else {
      this.setState({
        isOpen: !this.state.isOpen,
      });
    }
  }

  render() {
    const { yourname,
      showChatByMail,
      isChatStarted,
      onEndChat,
      agentProfile,
      onFilesSelected,
      onMessageWasSent,
      messageList,
      newMessagesCount, 
      isFullScreen } = this.props;
    const isOpen = this.props.hasOwnProperty('isOpen') ? this.props.isOpen : this.state.isOpen;
    const classList = [
      'sc-launcher',
      (isOpen ? 'opened' : ''),
    ];
    return (
      <div id="sc-launcher">
        <div className={classList.join(' ')} onClick={this.handleClick.bind(this)}>
          <MessageCount count={newMessagesCount} isOpen={isOpen} />
          <img className={'sc-open-icon'} src={launcherIconActive} alt="open" />
          <img className={'sc-closed-icon'} src={launcherIcon} alt="close" />
        </div>
        <ChatWindow
          messageList={messageList}
          onUserInputSubmit={onMessageWasSent}
          onFilesSelected={onFilesSelected}
          agentProfile={agentProfile}
          isOpen={isOpen}
          onClose={onEndChat}
          onMinimize={this.handleClick.bind(this)}
          isChatStarted={isChatStarted}
          yourname={yourname}
          showChatByMail={showChatByMail}
          isFullScreen={isFullScreen}
        />
      </div>
    );
  }
}

const MessageCount = (props: any) => {
  if (props.count === 0 || props.isOpen === true) { return null; }
  return (
    <div className={'sc-new-messages-count'}>
      {props.count}
    </div>
  );
};

